@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&display=swap");
.h-nav {
  background-color: #232f3e;
  height: 4rem;
  display: grid;
  grid-template-columns: 12% 88%;
}

.sll {
  height: 40px;
}

.sep2 {
  width: 360px;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 20px;
  color: #323235;
}

.sep3 {
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 20px;
  color: #323235;
}

.sep {
  text-align: center;
  justify-content: center;
  display: grid;
}

.eco-swaasa {
  display: grid;
  justify-content: center;
}

.eco-swaasa1 {
  display: grid;
  align-items: center;
  text-align: left;
  font-weight: bolder;
  font-size: 25px;
  padding-top: 3rem;
  color: orange;
  font-family: "IBM Plex Sans", sans-serif;
  justify-content: center;
  padding-bottom: 2rem;
}

.eco-products {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  justify-content: center;
}

.minus {
  padding-bottom: 2rem;
}

.minus2 {
  padding-top: 1rem;
}

.h-logo {
  color: orange;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  letter-spacing: 3px;
  //   background-color: #283e5b;
  //   text-shadow: 3px 3px 3px #232f3e;
}

.h-nactxt {
  color: #ccc;
}

.h-nactxt:hover {
  cursor: pointer;
}

.h-nactxt2:hover {
  cursor: pointer;
}

.h-nactxt2 {
  color: orange;
}

.r-right2 {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  padding-top: 0.5rem;
}

.h-right {
  display: grid;
  align-items: center;
}

.h-content2 {
  display: grid;
  justify-content: center;
}

.h-contentdiv {
  display: grid;
  justify-content: center;
  align-items: center;
}

.h-contenttext {
  display: grid;
  justify-content: center;
  align-items: center;
}

.texth {
  width: 400px;
  display: grid;
  justify-content: center;
  align-items: center;
  text-align: left;

  font-family: "IBM Plex Sans", sans-serif;
  font-size: 20px;
  color: #323235;
}

.textup2 {
  display: grid;
  align-items: center;
  text-align: left;
  font-weight: bold;
  font-size: 30px;
  padding-top: 4rem;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  justify-content: center;
}

@media (max-width: 600px) {
  .eco-products {
    grid-template-columns: unset;
  }
  .h-content2 {
    max-width: 100vw;
    grid-template-columns: unset;
  }

  .r-right2 {
    padding-top: 0.2rem;
  }

  .seclass {
    max-width: 100vw;
  }

  .textup2 {
    max-width: 100vw;
    display: grid;
    justify-self: center;
    font-size: 25px;
  }

  .texth {
    max-width: 100vw;
    text-align: center;
    padding: 0.5rem;
  }

  .h-content2 {
    max-width: 100vw;
    height: auto;
  }

  .texth {
    max-width: 90vw;
  }

  .textup2 {
    display: grid;
    justify-content: center;
  }

  .h-right {
    font-size: 12px;
  }

  .r-right2 {
    font-size: 12px;
    grid-template-columns: 1.3fr 1.3fr 2fr 2fr 2fr;
    justify-content: center;
    display: grid;
    padding-left: 0.5rem;
  }

  .h-nav {
    grid-template-columns: unset;
  }
}

@media screen and (min-width: 601px) and (max-width: 1024px) {
}
