.b-body {
  display: grid;
  justify-content: center;
  align-items: center;
  padding-bottom: 3rem;
  padding-top: 2rem;
}

.b-head {
  font-size: 30px;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: bolder;
  padding-top: 2rem;
}

.instructional {
  width: 800px;
  padding-top: 2rem;
}

.b-body-l {
  display: grid;
  justify-content: center;
  font-size: 20px;
  font-family: "IBM Plex Sans", sans-serif;
  color: #323235;
}

@media (max-width: 600px) {
  .b-body {
    grid-template-columns: unset;
  }

  .instructional {
    max-width: 90vw;
  }
}
