@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&display=swap");

.l-head {
  font-size: 30px;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: bolder;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.l-body {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.lname {
  padding-top: 1rem;
  font-family: "IBM Plex Sans", sans-serif;
  color: #323235;
  font-size: 20px;
  font-weight: bold;
}

.leaderpara {
  width: 300px;
  display: grid;
  padding-left: 6rem;
  padding-top: 3rem;
  font-size: 16px;
  font-family: "IBM Plex Sans", sans-serif;
  color: #323235;
  text-align: left;
}

@media (max-width: 600px) {
  .l-body {
    grid-template-columns: unset;
  }

  .l-head {
    max-width: 100vw;
  }

  .leaderpara {
    padding-left: 3rem;
    padding-bottom: 3rem;
    text-align: center;
  }
}

@media screen and (min-width: 601px) and (max-width: 1024px) {
  .l-body {
    grid-template-columns: unset;
  }
  .leaderpara {
    padding-left: 23rem;
    padding-bottom: 3rem;
  }
}
