@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans&display=swap");
.s2-head {
  background-color: #232f3e;
  position: fixed;
  width: 100%;
  height: 70px;
  top: 0;
  font-size: 30px;
  color: white;
  font-weight: bold;
  display: grid;
  justify-content: center;
  align-items: center;
  font-family: "IBM Plex Sans", sans-serif;
}

.step1ico {
  justify-content: center;
  display: grid;
  padding-top: 2rem;
}

.notepad {
  height: 3rem;
}

.s2-body {
  padding-top: 6rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;

  text-align: center;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 28px;
  color: #282c34;
  margin: 1rem;
}

.s2-root {
  display: grid;
  justify-content: center;
}

.fd-btn-div {
  margin-top: 3rem;
  display: grid;
  justify-content: center;
}

.gender-opt {
  color: #282c34;
  margin-top: 2rem;
  text-align: left;
}

.s-arrow {
  fill: rgba(0, 0, 0, 0.15);
}

.fd-btn {
  background: none;
  border: 2px solid #282c34;
  box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.15);
  color: #282c34;
  border-radius: 5px;
  width: 150px;
  height: 2.5rem;
  font-weight: bold;
}

.temp-opt {
  color: #282c34;
  margin-top: 2rem;
  text-align: left;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 1.2rem;
}

.smoking-opt {
  color: #282c34;
  margin-top: 2rem;
  text-align: left;
}

.s2-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.op1 {
  display: grid;
  justify-content: center;
  padding-top: 5rem;
}

.s2-age {
  padding-top: 2rem;
}

.s2-age-inp {
  text-align: center;
  box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.15);
  border: solid 1px #979797;
  height: 1.5rem;
  padding: 0.5rem;
  width: 15rem;
  border-radius: 2px;
}

.s2-gender {
  display: grid;
  justify-content: center;
  padding-top: 0rem;
  text-align: center;
}

.s-travel {
  display: grid;
  justify-content: center;
  text-align: center;
}

.s2-sym {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
}

.s2-contact {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
}

.s2-sym2 {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
}

.s2-smoking {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
}

.recscr-con {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr;
  padding-top: 2rem;
}

.resconr {
  width: 90%;
}

.resconl {
  width: 90%;
}

.s-recnext {
  display: grid;
  justify-content: center;
  text-align: center;
}

.recnextimg {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.s-progressed {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
}
.s2-temp {
  display: grid;
  justify-content: center;
  text-align: center;
}

.s2-countries {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-self: center;
}

.s2-relatedsyms {
  display: grid;
  justify-self: center;
  grid-template-columns: repeat(2, 1fr);
}

.s2-comorbid {
  display: grid;
  justify-self: center;
  grid-template-columns: repeat(3, 1fr);
}

.s-symlastweek {
  display: grid;
  justify-self: center;
  grid-template-columns: repeat(2, 1fr);
}

.comorbid-opt {
  margin-top: 1rem;
}
