@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&display=swap");

.s-head {
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 30px;
  padding-top: 3rem;
  font-weight: bold;
}

.s-img {
  padding-top: 2rem;
}

.s-screens {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-top: 4rem;
}

.s-headtext {
  text-align: center;
  display: grid;
  justify-content: center;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 20px;
  color: #323235;
}

.mp-s1 {
  display: grid;
  justify-content: center;
}

.s-head-inner {
  width: 800px;
}

.dimg1 {
  height: 350px;
  max-width: 100%;
}

@media (max-width: 600px) {
  .s-screens {
    grid-template-columns: unset;
    max-width: 100vw;
    padding-top: 2rem;
  }

  .s-head-inner {
    width: 90vw;
    text-align: center;
  }

  .dimg1 {
    max-width: 100vw;
    height: auto;
  }

  .dimg1 {
    padding-top: 2rem;
  }
}
