@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&display=swap");

.q1a {
  color: #323235;
  font-size: 20px;
  font-family: "IBM Plex Sans", sans-serif;
  text-align: left;
  width: 800px;
  text-align: center;
}

.h-head {
  font-size: 30px;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: bolder;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.h-body {
  display: grid;
  justify-content: center;
}

.howw {
  display: grid;
  justify-content: center;
}

.howimg {
  height: 300px;
}

.trak {
  height: 200px;
}

.how2 {
  display: grid;
}

.how2lup {
  font-size: 30px;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: bolder;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.how2ldown {
  color: #323235;
  font-size: 20px;
  font-family: "IBM Plex Sans", sans-serif;
  text-align: left;
  width: 800px;
  text-align: center;
}

.how2l {
  display: grid;
  justify-content: center;
}

.trakdiv {
  padding-top: 2rem;
}

@media (max-width: 600px) {
  .q1a {
    max-width: 90vw;
  }

  .how2lup {
    max-width: 100vw;
  }

  .how2ldown {
    max-width: 90vw;
    padding-left: 1.3rem;
    text-align: center;
  }

  .howimg {
    max-width: 100vw;
  }
}
