.co-body {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.co-head {
  font-size: 30px;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: bolder;
  padding-top: 2rem;
}

.corp-text {
  padding-top: 2rem;

  justify-content: center;
  display: grid;
}

.corp-text1 {
  padding-top: 2rem;
  width: 900px;
  font-family: "IBM Plex Sans", sans-serif;
  font-size: 20px;
  color: #323235;
}

@media (max-width: 600px) {
  .co-body {
    grid-template-columns: unset;
  }

  .corp-text1 {
    max-width: 90vw;
  }

  .co-head {
    max-width: 100vw;
  }

  .co-body {
    padding-top: 2rem;
  }

  .co-s {
    padding-top: 2rem;
  }

  .corp-text {
    padding-top: unset;
  }
}
