.l-body {
  display: grid;
  grid-template-columns: 1fr;
  padding-top: 8rem;
  justify-content: center;
}

.l-con {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
}

.lbtnc {
  display: grid;
  justify-content: center;
  padding-top: 3rem;
}

.lbtn {
  border: 2px solid #282c34;
  color: #282c34;
  outline: none;
  background-color: white;
  width: 10rem;
  height: 2rem;
  border-radius: 2px;
}

.l-inp {
  box-shadow: 0px 8px 20px 0px rgba(0, 0, 0, 0.15);
  border: solid 1px #979797;
  height: 1.5rem;
  padding: 0.5rem;
  width: 15rem;
  border-radius: 2px;
}
