@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&display=swap");

.pa-head {
  font-size: 30px;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: bolder;
  padding-top: 2rem;
  padding-bottom: 2rem;
}

.pa-body {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 3rem;
}

.patimgac {
  height: 250px;
}

.paimg12 {
  align-items: center;
  justify-content: center;
  padding-top: 3.5rem;
  font-size: 20px;
  font-family: "IBM Plex Sans", sans-serif;
  text-align: left;
}

.pubs {
  border-radius: 8px;
  background-color: #7a8cc4;
  margin-top: 2rem;
  color: white;
  font-family: "IBM Plex Sans", sans-serif;
  padding: 1rem;
  display: grid;
  justify-content: center;
  align-items: center;
  width: 900px;
}

.pat-btm {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
}

@media (max-width: 600px) {
  .pa-body {
    max-width: 100vw;
    grid-template-columns: unset;
  }
  .pat-btm {
    max-width: 100vw;
  }

  .pubs {
    max-width: 90vw;
  }

  .patimgac {
    max-width: 100vw;
  }

  .paimg12h {
    padding-left: 0.3rem;
    text-align: center;
  }
  .paimg12b {
    padding-left: 0.3rem;
    text-align: center;
  }
}

.paimg12h {
  color: #323235;
  font-family: "IBM Plex Sans", sans-serif;
}

.paimg12b {
  color: #323235;
  font-family: "IBM Plex Sans", sans-serif;
}

@media screen and (min-width: 601px) and (max-width: 1024px) {
  .pa-body {
    grid-template-columns: unset;
  }
}
