@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&display=swap");

.p-body {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 5rem;
}

.p-head {
  font-size: 30px;
  color: #7a8cc4;
  font-family: "IBM Plex Sans", sans-serif;
  font-weight: bolder;
  padding-top: 2rem;
}

.part-text {
  color: #323235;
  font-size: 20px;
  font-family: "IBM Plex Sans", sans-serif;
  text-align: left;
}

.hand {
  padding-right: 1rem;
}

.pb-r {
  display: grid;
}

@media (max-width: 600px) {
  .p-body {
    grid-template-columns: unset;
  }
  .part-text {
    display: grid;
    justify-content: center;
  }

  .handdiv {
    display: grid;
    justify-content: center;
    padding-top: 1rem;
  }

  .handdiv2 {
    text-align: center;
  }
}

.c-social {
  display: grid;
  justify-content: center;
  grid-column-gap: 20px;
}
