@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;500&display=swap");

.c-r {
  background-color: #232f3e;
}

.c-head {
  color: #7a8cc4;
  padding-top: 3rem;
  font-weight: bolder;
  font-size: 30px;
  font-family: "IBM Plex Sans", sans-serif;
}

.c-cbox {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding-bottom: 3rem;
  padding-top: 3rem;
  justify-content: center;
}

.cbox1 {
  display: grid;
  justify-content: center;
}

.c-foot {
  background-color: #4d6584;
  color: white;
  display: grid;
  grid-template-columns: 1fr;
}

.cbox1-width {
  width: 200px;
  display: grid;
  justify-content: center;
  color: white;
  text-align: left;
}

@media (max-width: 600px) {
  .c-cbox {
    grid-template-columns: 1fr;
    grid-row-gap: 2rem;
  }
  .c-foot {
    grid-template-columns: unset;
  }
  .c-copy {
    padding-top: 1rem;
  }
  .cbox1-width {
    text-align: center;
  }

  .c-social {
    padding-left: unset;
    padding-right: 5rem;
  }
}

.socialico {
  height: 30px;
  width: auto;
}

.socialc {
  display: grid;
  min-width: 30vw;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  padding-top: 1rem;
  grid-column-gap: 20px;
}

.c-social {
  padding-bottom: 1rem;
  padding-left: 5rem;
}

.c-copy {
  padding-top: 2rem;
  display: grid;
  justify-content: center;
  padding-bottom: 1rem;
}
