@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

.pa-head {
  display: grid;
  justify-content: center;
  padding-top: 2rem;
  font-family: "Roboto", sans-serif;
}

.medimg {
  height: 250px;
}

.med {
  justify-content: end;
  display: grid;
  padding-top: 2rem;
}

.par-inputs {
  justify-content: left;
  padding-left: 3rem;
}

.par-name {
  width: 300px;
  height: 2rem;
  padding-left: 1rem;
  border-radius: 4px;
  margin-top: 2rem;
}

.par-body {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.pa-btns {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 350px;
  padding-top: 2rem;
}

.red {
  justify-content: end;
  display: grid;
  padding-right: 2rem;
}

.instru {
  padding-top: 1rem;
  font-family: "Roboto", sans-serif;
}

.par-submit {
  justify-content: center;
  display: grid;
  padding-top: 4rem;
}

.submit-btn {
  width: 385px;
  height: 60px;
  border-radius: 5px;
  border: solid 1px #fafafa;
  background-color: #50d282;
  color: white;
  font-weight: bold;
  font-family: "Roboto", sans-serif;
}

@media (max-width: 600px) {
  .par-body {
    grid-template-columns: unset;
  }
  .med {
    max-width: 100vw;
    display: grid;
    justify-content: center;
  }
  .pa-head {
    display: grid;
    justify-content: center;
    text-align: center;
    font-size: 25px;
  }
  .medimg {
    width: 90vw;
  }
  .par-inputs {
    padding-left: 1.5rem;
  }
  .pa-btns {
    padding-top: 3rem;
  }
  .res {
    padding-bottom: 2rem;
  }
  .submit-btn {
    width: 100vw;
  }
}

.redb {
  color: red;
}

.nored {
  color: black;
}

.recstatus {
  padding-top: 1rem;
  width: 300px;
  display: grid;
  justify-content: center;
}
